<script>


  export default 
  {
  
    data () {
        return {
          ErrorIngresoDatos: false,
          MensajeError: "Sin error",
        }
    },
    computed: {
      ConfigPortal () 
      {
        return this.$store.state.ConfigPortal;
      }
    },
    mounted:function() 
    {
      //alert('updated');
      console.log("Portal turneros mounted:");
      console.log("route:");
      console.log(this.$route);
      if ((this.$route.query.wemp_pmla == undefined || this.$route.query.tema == undefined) && (this.$route.query.wemp_pmla == undefined || this.$route.query.portal == undefined))
      {
        this.MensajeError = "No se ingresaron los par&aacute;metros WEMP_PMLA (TEMA o PORTAL)";
        this.ErrorIngresoDatos = true;
        return;
      }

      if (this.$route.query.portal == undefined)
      {
        console.log("No Es Portal");
        this.redirect(`/Home`); 
        this.$store.dispatch("inicializarTurnero",{wemp_pmla:this.$route.query.wemp_pmla,tema:this.$route.query.tema,portal:this.$route.query.portal});
        return;
      }
      console.log("Es Portal");
      console.log(this.$store.state.ConfigPortal);
      this.$store.dispatch("inicializarTurnero",{wemp_pmla:this.$route.query.wemp_pmla,tema:this.$route.query.tema,portal:this.$route.query.portal});

    },
    methods: 
    {    
      redirect(parameter) 
      {
        this.$router.push(parameter);
      },

      clickTurnero(Turnero) 
      {
          this.$store.state.ConfigTurnero = Turnero;
          console.log(this.$store.state.ConfigTurnero);
          this.redirect(`/Home`); 
       },
  
    },
  }

  
</script>

